import * as d3 from 'd3'
import styles from '../components/Timeline/Timeline.module.css'
import { renderXAxis, renderYAxis } from './renderAxis'

export default (baseStruct, target) => {
    const {
        margin,
        width,
        height,
        xScale,
        yScale,
        min,
        max,
        dataset,
    } = baseStruct;

    xScale.domain(dataset.map((d) => d.date));
    yScale.domain([min - (max - min) / 2, max]);

    const svg = d3.select(target)
        .append('svg')
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .append('g')
        .attr('transform', 
            `translate(${margin.left}, ${margin.top})`);

    const barGroup = svg.selectAll('.bar')
        .data(dataset)
        .enter()
        .append('g')
    
    const bar = barGroup.append('rect')
        .attr('class', styles.bar)
        .attr('x', (d) => xScale(d.date))
        .attr('width', xScale.bandwidth())
        .attr('y', () => height)
        .attr('fill', 'steelblue')
        .attr('height', () => 0)
  
    bar.transition()
        .duration(1000)
        .attr('y', (d) => yScale(d.value))
        .attr('height', (d) => height - yScale(d.value))

    bar.on('mouseenter', (actual) => {
        barGroup.append('text')
            .attr('text-anchor', 'middle')
            .attr('fill', 'white')
            .attr('y', 1)
            .attr('x', xScale(actual.date) + xScale.bandwidth() / 2)
            .attr('y',  yScale(actual.value) - 5)
            .text(actual.value) 
        svg.append('line')
            .attr('id', 'limit')
            .attr('x1', 0)
            .attr('y1', yScale(actual.value))
            .attr('x2', width)
            .attr('y2', yScale(actual.value))
            .attr('stroke', 'red')
        })
        
        .on('mouseleave', () => {
            svg.selectAll('#limit')
            .remove()
            barGroup.selectAll('text')
            .remove()
        });

    renderXAxis(svg, height, xScale);      
    renderYAxis(svg, yScale);
}
