import * as d3 from 'd3'
import React from 'react'
import styles from './Timeline.module.css'
import renderBarChart from '../../render/renderBarChart'
import renderMonotoneInterpolation from '../../render/renderMonotoneInterpolation'
import createStruct from '../../render/createStruct'

export class Timeline extends React.Component {
    renderGraph = (dataset) => {
        const baseStruct = createStruct(dataset);
        d3.select('svg').remove();
        if (this.props.checkbox) {
            renderMonotoneInterpolation(baseStruct, this.refs.target);
        } else {
            renderBarChart(baseStruct, this.refs.target);
        }
    }

    componentDidUpdate(prevData) {
        const currentData = JSON.stringify(this.props.dataset);
        const previousData = JSON.stringify(prevData.dataset);

        if ((currentData !== previousData ||
            prevData.checkbox !== this.props.checkbox) &&
            currentData !== undefined) {
            this.renderGraph(this.props.dataset)
        }     
    }

    componentDidMount() {
       this.renderGraph(this.props.dataset)
    }

    render () {
        return (
            this.props.dataset !== undefined ?
                <div className={styles.timeline}ref='target'></div>  
                : <p>Введите корректный диапазон</p>
        )
    }
}