const convert = require('xml-js');
const proxyurl = "https://cors-anywhere.herokuapp.com/";


export const cbParse = (dateStart, dateEnd, currency) =>{
    let data;
    let dataObj = [];
    const url = `http://www.cbr.ru/scripts/XML_dynamic.asp?date_req1=${dateStart}&date_req2=${dateEnd}&VAL_NM_RQ=${currency}`;

    data = fetch(proxyurl + url)
    .then(response => response.text())
    .then(contents => convert.xml2json(contents, {compact: true, spaces: 2}))
    .then(contents => JSON.parse(contents))
    .then(contents => contents["ValCurs"]["Record"])
    .catch(() => console.log("Cant access " + url + " response."))
    .then(contents => {
        contents.forEach(element => {
            let newObj = {};
            newObj.date = element._attributes.Date;
            newObj.value = parseFloat(element.Value._text.replace(',', '.'));
            dataObj.push(newObj);
        });
        return dataObj;
    }).catch(() => console.log("Cant access " + url + " response."));
    return data;
}
