import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';

/* import { neurokek } from './helpers/neuroKek.js'; */
if (localStorage.getItem('dateStart') == null) localStorage.setItem('dateStart', '');
if (localStorage.getItem('dateEnd') == null) localStorage.setItem('dateEnd', '');
if (localStorage.getItem('currency') == null) localStorage.setItem('currency', 'R01010');
if (localStorage.getItem('data') == null) localStorage.setItem('data', '');
if (localStorage.getItem('checkbox') == null) localStorage.setItem('checkbox', true);

ReactDOM.render(<App />, document.getElementById('root'));

serviceWorker.unregister();
