import * as d3 from 'd3'

export default (dataset) => {
    const margin = {
        top:    20,
        right:  20,
        bottom: 60,
        left:   40
    };

    const datasetLength = dataset.length;

    const width = 600 + (datasetLength * 20) - margin.left - margin.right;
    const height = 500 - margin.top - margin.bottom;

    const xScale = d3.scaleBand()
        .range([0, width])
        .padding(0.1);

    const yScale = d3.scaleLinear()
        .range([height, 0]);
    
    const min = d3.min(dataset, (d) => d.value);

    const max = d3.max(dataset, (d) => d.value );
    
    return ({
        'margin': margin,
        'width': width,
        'height': height,
        'xScale': xScale,
        'yScale': yScale,
        'min': min,
        'max': max,
        'dataset': dataset
    });
}
