import React from 'react'
import styles from './DateInput.module.css'
import currentDate from '../../helpers/currentDate.js'

export default props => {
    return (
        <div>
            <input
                value={props.value}
                required={true}
                min={'1992-01-07'}
                max={currentDate()}
                type="date"
                className={styles.dateInput}
                onChange={props.onChange}
                name={props.name}
            />
        </div>  
    );
}
