import * as d3 from 'd3';
import { renderXAxis, renderYAxis } from './renderAxis'
import styles from '../components/Timeline/Timeline.module.css'

export default (baseStruct, target) => {
    const {
        margin,
        width,
        height,
        xScale,
        yScale,
        min,
        max,
        dataset,
    } = baseStruct;

    xScale.domain(dataset.map((d) => d.date));
    yScale.domain([min - (max - min) / 2, max]);

    const svg = d3.select(target)
        .append('svg')
        .attr('width', width + margin.left + margin.right)
        .attr('height', height + margin.top + margin.bottom)
        .append('g')
        .attr('transform', 
            `translate(${margin.left}, ${margin.top})`);

    const line = d3.line()
        .x(function(d) { return xScale(d.date); })
        .y(function(d) { return yScale(d.value); })
    
    const path = svg.append('path')
        .datum(dataset)
    
    path.attr('fill', 'none')
        .attr('stroke', 'steelblue')
        .attr('stroke-width', 1.5)
        .attr('d', line)
        .attr('transform', 
            `translate(${margin.left - 25}, ${margin.top})`)
        .attr('opacity', 0)
        .transition()
        .duration(1500)
        .attr('opacity', 1)
        
    const dots = svg.selectAll('.dot')
        .data(dataset)
        .enter()
        .append('circle')

    dots.attr('stroke', 'steelblue')
        .attr('class', styles.dot)
        .attr('cx', line.x())
        .attr('cy', height)
        .attr('r', 5)
        .attr('transform', 
            `translate(${margin.left - 25}, ${margin.top})`)
        .transition()
        .duration(800)
        .attr('cy', line.y());
    
        dots.on('mouseenter', (actual) => {
            svg.append('text')
            .attr('class', 'dottext')
            .attr('x', xScale(actual.date))
            .attr('y', yScale(actual.value))
            .attr('fill', 'white')
            .text(`${actual.date}: ${actual.value}`)
        })
        .on('mouseleave', () => {
            svg.selectAll('.dottext')
            .remove()
        })

    renderXAxis(svg, height, xScale);      
    renderYAxis(svg, yScale);
}