import React from 'react'
import styles from './Toggle.module.css'

export default (props) => {
    return (
        <div>
            Bar <label className={styles.switch}>
                <input
                    onChange={props.onChange}
                    checked={props.checked}
                    type="checkbox"
                />
                <span className={styles.slider}></span>
            </label> Lin
        </div> 
    )
}
