import React from 'react'
import './App.css'

import { cbParse } from './helpers/cbParse'
import dateFormat from './helpers/dateFormat'

import Header from './components/Header/Header'
import InputDataForm from './components/InputDataForm/InputDataForm'
import Loader from './components/Loader/Loader'
import Footer from './components/Footer/Footer'
import Toggle from './components/Toggle/Toggle'
import { Timeline  } from './components/Timeline/Timeline'

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dateStart:  localStorage.getItem('dateStart'),
            dateEnd:    localStorage.getItem('dateEnd'),
            currency:   localStorage.getItem('currency'),
            data:       localStorage.getItem('data').length !== 0 ||
                        localStorage.getItem('data').length === undefined?
                            JSON.parse(localStorage.getItem('data'))
                            : [],
            loaded:     true,
            checkbox:   JSON.parse(localStorage.getItem('checkbox')),
        }
    }

    fetchData = () => {
        this.setState({ loaded: false});
        cbParse(
            dateFormat(this.state.dateStart),
            dateFormat(this.state.dateEnd),
            dateFormat( this.state.currency)
        ).then(apidata => {
            this.setState({
                data: apidata,
                loaded: true,
            });
            localStorage.setItem('data', JSON.stringify(apidata));
        })
    }

    handleSubmit = event => {
        event.preventDefault();
        this.fetchData();
    }

    handleToggleChange = event => {
        this.setState({
            checkbox: event.target.checked
        })
        localStorage.setItem('checkbox', event.target.checked);
    }

    handleInputChange = event => {
        const { value, name } = event.target;

        if (name === 'currency') {
            this.setState({
                [name]: value
            });
            localStorage.setItem(name, value);
        } else {
            this.setState({
                [name]: value
            });
            localStorage.setItem(name, value);
        } 
    }

    renderTimelinePage = routeProps =>
        <Timeline
            {...routeProps}
            dataset={this.state.data}
            checkbox={this.state.checkbox}
        />

    render() {
        return (
            <div className="App">
                <Header
                    name='PriceGraph'    
                />
                <main className="App-body">
                    <InputDataForm
                        checkbox={this.state.checkbox}
                        handleInputChange={this.handleInputChange}
                        handleSubmit={this.handleSubmit}
                        dateStart={this.state.dateStart}
                        dateEnd={this.state.dateEnd}
                        selectedCurrency={this.state.currency}
                    />
                    <Toggle
                        checked={this.state.checkbox}
                        onChange={this.handleToggleChange}
                    />
                    <Loader
                        loaded={this.state.loaded}
                    />                    
                    <Timeline
                        dataset={this.state.data}
                        checkbox={this.state.checkbox}
                    />
                    <Footer/>
                </main> 
            </div>
        );
    }
}

export default App;
